import { Share } from "components/Share";
import React from "react";
import { FC, useEffect } from "react";
import { Helmet } from "react-helmet";
import ProductCard from "../components/ProductCard/ProductCard";
import { useActionCreators } from "../hooks/useAC";
import { UseTypedSelector } from "../hooks/useTypedSelector";
import loseImg from "../assets/icons/lose-icon.png";
import { MetaDecorator } from "components/MetaDecorator";
const ProductContent: FC = () => {
  const { getProductsThunk } = useActionCreators();
  const { products } = UseTypedSelector((state) => state.ProductContent);

  useEffect(() => {
    getProductsThunk();
  }, []);
  const path = window.location.pathname + window.location.search;

  return (
    <>
      {/* <Helmet defer={false} onChangeClientState={(state) => console.log(state)}> */}
      {/* <!-- Primary Meta Tags --> */}
      <MetaDecorator
        description={"Hello"}
        title={"Products"}
        imageUrl={loseImg}
        imageAlt={"img"}
      />
      {/* </Helmet> */}
      <div className="container">
        <div className="ProductContent_items" style={{ paddingTop: "20px" }}>
          {products.map((product) => (
            <ProductCard key={product.productName} {...product} />
          ))}
          <div>
            <a
              href="https://www.facebook.com/sharer.php?u=https://liu1331.github.io/test.github.io-/"
              target="_blank"
              style={{
                display: "flex",
                width: "15px",
                height: "15px",
                background: "black",
              }}
            >
              <i className="fab fa-facebook fa-3x"></i>
            </a>
            <a
              href="https://twitter.com/share?url=https://liu1331.github.io/test.github.io-/"
              target="_blank"
              style={{
                display: "flex",
                width: "15px",
                height: "15px",
                background: "black",
              }}
            >
              <i className="fab fa-twitter-square fa-3x"></i>
            </a>
            <a
              href="https://www.linkedin.com/shareArticle?url=https://liu1331.github.io/test.github.io-/"
              target="_blank"
              style={{
                display: "flex",
                width: "15px",
                height: "15px",
                background: "black",
              }}
            >
              <i className="fab fa-linkedin fa-3x"></i>
            </a>
          </div>
        </div>
      </div>
    </>
  );
};

export default ProductContent;
